body{ margin:0; padding:0; height:100%; width:100%; }
.full-screen{
  display: flex;
  flex-direction: column;
  max-width: 300px;
  margin: auto;
}

.mxw-500{
  max-width: 500px;
}

.login{
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Full-width inputs */
input[type=text], input[type=password] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

/* Set a style for all buttons */
button {
  background-color: #04AA6D;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
}

/* Add a hover effect for buttons */
button:hover {
  opacity: 0.8;
}

/* Extra style for the cancel button (red) */
.cancelbtn {
  width: auto;
  padding: 10px 18px;
  background-color: #f44336;
}

/* Center the avatar image inside this container */
.imgcontainer {
  text-align: center;
  margin: 24px 0 12px 0;
}

/* Avatar image */
img.avatar {
  width: 40%;
  border-radius: 50%;
}

/* Add padding to containers */
.container {
  padding: 16px;
}

/* The "Forgot password" text */
span.psw {
  float: right;
}

/* Change styles for span and cancel button on extra small screens */
@media screen and (max-width: 300px) {
  span.psw {
    display: block;
    float: none;
  }
  .cancelbtn {
    width: 100%;
  }
}

.error_msg{
  width: inherit;
  color: #f91515;
  font-size: 14px;
}

.login-footer{
  display: flex;
  justify-content: space-between;
  padding: 16px;
  padding-top: 0px;
}

.dropdown-signup{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}